import gsap from "gsap";
import React, { useRef } from "react";
import { useLayoutEffect } from "react";

export const Header = ({ scrollToAbout }) => {
  const headerRef = useRef(null);
  const divRef = useRef(null);
  const bottomRef = useRef(null);

  useLayoutEffect(() => {
    gsap.fromTo(
      headerRef.current,
      { opacity: 0 }, // Starting state
      {
        opacity: 1, // Ending state
        duration: 2, // Duration of 2 seconds
        delay: 2, // Start after 2 seconds
        ease: "power2.out", // Smooth easing
      }
    );
    gsap.to(divRef.current, {
      backgroundPosition: "50% 0px", // Move vertically (Y-axis parallax)
      ease: "none", // No easing for smooth parallax
      scrollTrigger: {
        trigger: divRef.current, // Element that triggers the animation
        start: "top bottom", // Start when top of element hits bottom of viewport
        end: "bottom top", // End when bottom of element hits top of viewport
        scrub: true, // Smoothly scrub animation with scroll
      },
    });

    gsap.to(divRef.current, {
      scrollTrigger: {
        trigger: divRef.current,
        start: "bottom center", // Trigger when the bottom of the divRef.current reaches the center of the viewport
        end: "bottom top", // End when the bottom of the divRef.current leaves the top of the viewport
        toggleClass: { targets: divRef.current, className: "black-bg" }, // Add or remove the "black-bg" class
        toggleActions: "play reverse play reverse", // Ensures smooth transitions both ways
        // markers: true, // For debugging
      },
    });
    // gsap.to(divRef.current, {
    //   scrollTrigger: {
    //     trigger: bottomRef.current,
    //     start: "bottom center", // Trigger when the bottom of the divRef.current reaches the center of the viewport
    //     end: "top center", // End when the bottom of the divRef.current leaves the top of the viewport
    //     toggleClass: { targets: divRef.current, className: !"img-bg" }, // Add or remove the "black-bg" class
    //     markers: true, // For debugging
    //   },
    // });
  }, []);
  return (
    <header className="header" ref={divRef}>
      <div>
        <h1 ref={headerRef}>I'm a Travel Blogger</h1>
      </div>
      <div className="header__nav">
        <button onClick={scrollToAbout}>Articles</button>
        <p>Articles</p>
        <p>Articles</p>
      </div>
      <div ref={bottomRef} style={{ paddingTop: "100px" }}></div>
    </header>
  );
};
